;(function(window, $, undefined){

	var colors = APP.palette.pick()

	$('#progetto').css('background-color', colors.c2.a)
	$('#welcome').css('background-color', colors.c1.a)
	$('#board').css('background-color', colors.c2.a)
	
	var style = ''
	style += '.a{border-color:'+colors.c2.b+';color:'+colors.c2.b+';} .b{border-color:'+colors.c1.b+';color:'+colors.c1.b+';}'
	style += '.logo.a text{fill:'+colors.c2.b+';} .logo.b text{fill:'+colors.c1.b+';}'
	style += '.fga.a path{fill:'+colors.c2.b+';} .a line{stroke:'+colors.c2.b+';}'
	style += '.social.a path{fill:'+colors.c2.a+';}'
	style += '#messages .a{background-color:'+colors.c2.a+';} #messages .isnew > div{border-color:white;background-color:'+colors.c2.b+';color:'+colors.c2.a+';}'
	style += 'a.a{color:'+colors.c2.b+'} a.a{color:'+colors.c2.b+'} '
	style += 'a.b{color:'+colors.c1.b+'} a.b{color:'+colors.c1.b+'} '
	style += 'a.a path{fill:'+colors.c2.b+'} a.a path{fill:'+colors.c2.b+'} '
	style += 'a.b path{fill:'+colors.c1.b+'} a.b path{fill:'+colors.c1.b+'} '
	style += '.mouse a.a:hover span{color:'+colors.c2.a+'; background-color:'+colors.c2.b+';} .mouse a.a:hover span{color:'+colors.c2.a+'; background-color:'+colors.c2.b+';} '
	style += '.mouse a.b:hover span{color:'+colors.c1.a+'; background-color:'+colors.c1.b+';} .mouse a.b:hover span{color:'+colors.c1.a+'; background-color:'+colors.c1.b+';} '
	$('head').append('<style>'+style+'</style>')


})(window, window.jQuery)


