(function addAnalytics(){
	
	$(document).ready(function(){

		var userAgree = window.docCookies.getItem('doNotProfile');

		if(userAgree){
			addTheAnalytics()
		}else{
			$('[js_ok_cookie]').on('click', function(){
				$('[js_ok_cookie]').off('click')
				addTheAnalytics()
			})
		}


		function addTheAnalytics(){

			//$('[js_ok_cookie]').off('click')
			
			(function(i,s,o,g,r,a,m){i['GoogleAnalyticsObject']=r;i[r]=i[r]||function(){
			(i[r].q=i[r].q||[]).push(arguments)},i[r].l=1*new Date();a=s.createElement(o),
			m=s.getElementsByTagName(o)[0];a.async=1;a.src=g;m.parentNode.insertBefore(a,m)
			})(window,document,'script','//www.google-analytics.com/analytics.js','ga');
			ga('create', 'UA-90539463-1', 'auto');
			ga('send', 'pageview');

			window.addEventListener('scroll', scrolled);

			!function(f,b,e,v,n,t,s)
	        {if(f.fbq)return;n=f.fbq=function(){n.callMethod?
	        n.callMethod.apply(n,arguments):n.queue.push(arguments)};
	        if(!f._fbq)f._fbq=n;n.push=n;n.loaded=!0;n.version='2.0';
	        n.queue=[];t=b.createElement(e);t.async=!0;
	        t.src=v;s=b.getElementsByTagName(e)[0];
	        s.parentNode.insertBefore(t,s)}(window,document,'script',
	        'https://connect.facebook.net/en_US/fbevents.js');
	        fbq('init', '619579071566098'); 
	        fbq('track', 'PageView');

			$('body').on('click', function(){
				ga('send', 'event', 'interaction', 'click');
			})

			function scrolled(){
				ga('send', 'event', 'interaction', 'scroll');
				window.removeEventListener('scroll', scrolled);	
			}

			
		}

	})
	
})();