;(function(window, $, undefined){

	window.APP = {}

	var navs = ["progetto", "contribuisci", "bacheca"]

	APP.goNext = function(){
		var index = navs.indexOf(stator.current.currentName)
		index++
		if(index<navs.length){
			stator.go(navs[index])
		}
	}

	APP.goPrev = function(){
		var index = navs.indexOf(stator.current.currentName)
		index--
		if(index>-1){
			stator.go(navs[index])
		}
	}


	if(window.isMobile){
		$('#messages').removeClass('stacked')
		$('#messages_template > li').removeClass('stacked')
	}


	$(document).ready(function(){

		var h = $('body').height()
		$('#loader').transition({y:-h, delay:.5, complete:function(){
			$('#loader').addClass('hidden')
		}}, 750, 'easeInOutQuint')

	})
	

	

})(window, window.jQuery)


