;(function(window, $, undefined){

	var labels = [{top:'', bot:'Dillo tu!'}, {top:'Il progetto', bot:'La bacheca'}, {top:'Dillo tu!', bot:''}]
	var urls = [{top:'', bot:'contribuisci'}, {top:'progetto', bot:'bacheca'}, {top:'contribuisci', bot:''}]
	var classes = ['a', 'b', 'a']
	var currSec = 0
	var inTransit = false
	var checkTransit

	function _slideSectionOnly(sec){

		var h = $('article').height()

		var dir = currSec - sec
		currSec=sec

		var reverseDelay = (dir == -1) ? 0 : 140

		$('#progetto').transition({y:sec*h*-1, delay:Math.abs(reverseDelay-10)}, 800, 'easeInOutQuint')
		$('#welcome').transition({y:sec*h*-1, delay:Math.abs(reverseDelay-70)}, 800, 'easeInOutQuint')
		$('#board').transition({y:sec*h*-1, delay:Math.abs(reverseDelay-130)}, 800, 'easeInOutQuint')
		
	}

	function _updateNav(sec){
		TweenLite.to($('[js_btn_top] span'), 2, {scrambleText:labels[sec].top, chars:"XO", speed:0.5}); 
		TweenLite.to($('[js_btn_bot] span'), 2, {scrambleText:labels[sec].bot, chars:"XO", speed:0.5}); 

		$('[js_btn_top]').attr('href', '#/' + urls[sec].top)
		$('[js_btn_bot]').attr('href', '#/' + urls[sec].bot)

		$('[js_btn_top]').removeClass('a b')
		$('[js_btn_bot]').removeClass('a b')

		$('[js_btn_top]').addClass(classes[sec])
		$('[js_btn_bot]').addClass(classes[sec])

		$('header').css({opacity:1})
		$('footer').css({opacity:1})

		if(sec == 0){
			$('header').transition({opacity:0, delay:500}, 500)
		}
		if(sec == 2){
			$('footer').transition({opacity:0, delay:500}, 500)
		}
	}

	function _slideSection(sec){
		inTransit = true
		_slideSectionOnly(sec)
		_updateNav(sec)
		checkTransit = setTimeout(function(){
			inTransit=false
			_consolidateSectionPosition(sec)
		}, 1000)
	}


	function _slideNext(){
		if(currSec<2) _slideSection(currSec+1)
	}

	function _slidePrev(){
		if(currSec>0) _slideSection(currSec-1)
	}


	$(window).resize(function(){
		if(!inTransit){
			_consolidateSectionPosition(currSec)
		}
	})

	function _consolidateSectionPosition(sec){
		var h = $('article').height()
		$('#progetto').css({y:sec*h*-1})
		$('#welcome').css({y:sec*h*-1})
		$('#board').css({y:sec*h*-1})
	}

	_consolidateSectionPosition(1)

	$('header').css({opacity:0})
	$('footer').css({opacity:0})

	// var h = $('article').height()
	// $('#progetto').css({y:h*-1})
	// $('#welcome').css({y:h*-1})
	// $('#board').css({y:h*-1})


	APP.slideSection = _slideSection;
	APP.slideSectionOnly = _slideSectionOnly;
	APP.updateNav = _updateNav;
	APP.slideNext = _slideNext;
	APP.slidePrev = _slidePrev;
	

})(window, window.jQuery)




