function Contribuisci(){

	$('#innerForm').addClass('silent')
	var messageSubmitted = true

	var txtlimit = 40
	var input = $('#welcome').find('textarea')


	function _onKeys(e){
		var txt = input.val()
		if(txt){
			if(txt.length>0){
				if(txt.length>=txtlimit){
					var truncated = txt.substr(0, txtlimit)
					txt = truncated
					input.val(truncated)
					TweenLite.to($('#counter'), .75, {overwrite:true, delay:.5, scrambleText:'Hai raggiunto la lunghezza limite', chars:"XO", speed:0.5});
				}else{
					$('#msg_go').removeClass('hidden')
					TweenLite.to($('#msg_go'), 1, {scrambleText:'Pubblica', chars:"XO", speed:0.5});

					var num = txtlimit - txt.length
					var lastWord = (num == 1) ? 'carattere' : 'caratteri'
					TweenLite.to($('#counter'), .75, {overwrite:true, delay:.5, scrambleText:'Hai ancora ' + num + ' ' + lastWord, chars:"XO", speed:0.5});
				}
			}else{
				$('#msg_go').addClass('hidden')
			}
		}
	}

	function _onKeyPress(e){
		var cd = e.keyCode ? e.keyCode : e.which
        if(cd == 13){
			var txt = input.val()
			if(txt && txt.length>0){
            	msgProceede(txt)
        	}
            e.preventDefault()
        }
	}


	function _clickPublish(){
		var txt = input.val()
        if(txt && txt.length>0){
            msgProceede(txt)
        }
        return false
	}


	function msgProceede(msg){
		console.log('msgProceede', msg)
		input.attr('data-placeholder', 'Scrivilo in bacheca...')
		var dummy = $('<p></p>').text(input.val())
		TweenLite.to(dummy, 2, {scrambleText:'', chars:"XO", speed:0.5, onUpdate:function(){
			input.val(this.target.text())
		}});
		APP.fb.createNewMessage(msg)
		messageSubmitted=true
		input.blur()
	}

	

	return {
		enter: function(option){
			console.log('Contribuisci :: enter')

			APP.slideSection(1)

			if(messageSubmitted){
				$('#msg_go').addClass('hidden')
			}else{
				$('#msg_go').css({opacity:0})
				$('#msg_go').transition({opacity:1, delay:800}, 1000, 'easeInOutQuint')
			}

			$('#innerForm').removeClass('silent')
			$('#innerForm').css({opacity:1})

			$('#innerForm textarea').css({y:50, opacity:0})
			$('#innerForm textarea').transition({y:0, opacity:1, delay:600}, 1000, 'easeInOutQuint')

			$('#innerForm .logo').css({y:-50, opacity:0})
			$('#innerForm .logo').transition({y:0, opacity:1, delay:800}, 1000, 'easeInOutQuint')

			$('#innerForm #counter').css({y:50, opacity:0})
			$('#innerForm #counter').transition({y:0, opacity:1, delay:800}, 1000, 'easeInOutQuint')

			
			if(messageSubmitted){
				var txt = $('#innerForm #counter').data('init')
				$('#innerForm #counter').text(txt)
				input.introducing({delay:1000})
			}

			messageSubmitted=false


			input.on('change keyup paste', _onKeys)
			input.on('keypress', _onKeyPress)
			$('#msg_go').on('click', _clickPublish)



			

		},
		leave: function(option){
			console.log('Contribuisci :: leave')
			$('#msg_go').off('click', _clickPublish)
			input.off('keypress', _onKeyPress)
			input.off('change keyup paste', _onKeys)

			$('#innerForm').addClass('silent')
			$('#innerForm').css({opacity:0})

		}
	}

}
