function Bacheca(){

  	function animateMessages(){
		var interv
		if($('#messages li').length == 0){
			$('#messages').css({opacity:0})
			interv = setInterval(function(){
				if($('#messages li').length > 0){
					$('#messages').css({opacity:1})
					_animateMessages()
					clearInterval(interv)
				}
			}, 300)
		}else{
			_animateMessages()
		}
	}


	function _animateMessages(){

		var msg = $('#messages li')

		var w = $('article').width()
        var h = $('article').height()
        
        var padw = w*.05
        var padh = h*.1

		msg.css({opacity:0})
		msg.each(function(i, e){

			var e = $(e)

			if(e.hasClass('isnew')){
				e.css({y:30})
				e.transition({y:0, opacity:1, delay:2000}, 1000)

			}else{
				var c = $(e.children()[0])
				var cw = c.outerWidth()/2
				var ch = c.outerHeight()/2
				var fw = (w/2 - cw - padw)*2
				var fh = (h/2 - ch - padh)*2
				e.transition({x:padw+cw+Math.random()*fw-w/2, y:padh+ch+Math.random()*fh-h/2, opacity:1, delay:600+i*80}, 1000, 'easeInOutQuint')
			}
		})
	}






	function setSocialActions(txt){
		var i = 575
	    var n = 350
	    var r = screen.width / 2 - i / 2
	    var s = screen.height / 2 - n / 2

		var tw = 'https://twitter.com/intent/tweet?'
        tw += 'url=' + encodeURIComponent('http://ilfuturo.it')
        tw += '&text=' + encodeURIComponent('#ilfuturo ' + txt)

        var fb = 'https://www.facebook.com/sharer/sharer.php?'
        fb += 'u=' + encodeURIComponent('http://ilfuturo.it')

        $('#tw').on('click', function(e){
			window.open(tw, 
	          "_blank", 
	          "height=" + n + ",width=" + i + ",top=" + s + ",left=" + r)
			e.preventDefault()
		})
		$('#fb').on('click', function(e){
			window.open(fb, 
	          "_blank", 
	          "height=" + n + ",width=" + i + ",top=" + s + ",left=" + r)
			e.preventDefault()
		})
	}



	return {
		enter: function(option){
			console.log('Bacheca :: enter')
			APP.slideSection(2)
			if(!window.isMobile) animateMessages()

			var isnew = $('#messages .isnew')
			if(!isnew.hasClass('alreadySocial')){
				setSocialActions(isnew.find('p').text())
				isnew.addClass('alreadySocial')
			} 

			$('#board').scrollTop( 0 );
		},
		leave: function(option){
			console.log('Bacheca :: leave')
		}
	}

}
