function Progetto(){

	var h = $('article').height()
	$('#innerProject form').css('max-height', h*.65)

	return {
		enter: function(option){
			console.log('Progetto :: enter')
			APP.slideSection(0)

			$('#innerProject form').scrollTop( 0 );

			$('#innerProject form').css({y:50, opacity:0})
			$('#innerProject form').transition({y:0, opacity:1, delay:600}, 1000, 'easeInOutQuint')

			$('#innerProject .logo').css({y:-50, opacity:0})
			$('#innerProject .logo').transition({y:0, opacity:1, delay:800}, 1000, 'easeInOutQuint')

			$('#innerProject .fga').css({y:50, opacity:0})
			$('#innerProject .fga').transition({y:0, opacity:1, delay:800}, 1000, 'easeInOutQuint')
		},
		leave: function(option){
			console.log('Progetto :: leave')

		}
	}

}
