;(function($){
    
    
    function init(e, settings){

        var el = $(e)
        el.attr('placeholder', null)
        el.removeClass('first')

        var original_str = el.data('placeholder')
        var index = 0
        var area = $('textarea')
        var current_str = ''
        var interv
        var timr
        
        if(original_str){
            timr = setTimeout(function(){
                interv = setInterval(function(){
                    el.val(current_str)
                    current_str += original_str.substr(index, 1)
                    index++
                    if(index > original_str.length){
                        clearInterval(interv)
                        el.addClass('done')
                    } 
                }, 66)
            }, settings.delay)
        }
        
        if(!el.data('introducing')){
            el.focusin(function(){
                clearInterval(interv)
                clearTimeout(timr)
                if(!$(this).hasClass('first')){
                    el.val('')
                    el.addClass('first')
                    el.removeClass('done')
                    el.attr('placeholder', original_str)
                }
            })

            el.attr('data-introducing', "true")
        }

    }

    $.fn.introducing = function(options) {
        
        var defaults = {delay:0};
        var settings = $.extend( {}, defaults, options );
        
        return this.each(function(i, e){
            init(e, settings)
        });
    };

})(jQuery)