;(function(window, $, undefined){

    APP.fb = {}

	firebase.initializeApp(config)

    var ref = firebase.database().ref('approved-posts')

    var messages = []

    var templateForNew
    var templateForCur

    $(document).ready(function(){
        templateForNew = $('#messages_template li:first-child').clone()
        templateForCur = $('#messages_template li:last-child').clone()
    })
    

    ref.limitToLast(30).once('value')
    	.then(function(snapshot) {
    		var res = snapshot.val()
		    for(var k in res){
                messages.push(res[k])
		    }
		    populateItems()
		})


	function populateItems(){
		$('#messages').empty()
		for(var k in messages){
            var item = (messages[k].just) ? templateForNew.clone() : templateForCur.clone()
            item.attr('data-text', messages[k].body)
            var p = item.find('p')
            p.html(messages[k].body + '<span>|</span>')
            $('#messages').append(item)
            messages[k].just = false
        }

	}




    function createNewMessage(msg){

        var postData = {
          author: '',
          body: msg,
          date: new Date().toISOString()
        }


        var newPostKey = firebase.database().ref().child('pending-posts').push().key

        var updates = {}
        updates['/pending-posts/' + newPostKey] = postData

        firebase.database().ref().update(updates)

        postData.just = true
        messages.unshift(postData)
        populateItems()

        window.stator.go('bacheca')
    }


    APP.fb.createNewMessage = createNewMessage


})(window, window.jQuery)