function Welcome(){

	$('#innerWelcome').css({opacity:0})
	$('#innerWelcome').addClass('silent')

	return {
		enter: function(option){
			console.log('Welcome :: enter')
		
			$('#innerWelcome').removeClass('silent')
		
			$('#innerWelcome').css({opacity:1})
			$('#innerWelcome > h1').css({opacity:0, y:50})
			$('#innerWelcome .js_a').transition({y:0, opacity:1, delay:50}, 2000, 'easeInOutQuint')
			$('#innerWelcome .js_b').transition({y:0, opacity:1, delay:500}, 2000, 'easeInOutQuint')

			APP.slideSectionOnly(1)

		},
		leave: function(option){
			console.log('Welcome :: leave')

			$('#innerWelcome').addClass('silent')

			$('#innerWelcome').transition({opacity:0, delay:1000}, 1000, 'easeInOutQuart')
			$('#innerWelcome .js_a').transition({y:-50, opacity:0, delay:0}, 750, 'easeInOutQuart')
			$('#innerWelcome .js_b').transition({y:-50, opacity:0, delay:300}, 750, 'easeInOutQuart')

		}
	}

}

